@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.minichart{
    height: 125px;
    width: 60%;
    left: auto;
    right: auto;
    margin-left: auto;
    margin-right: auto;
    padding: 5px;
}
.container{
    width: 100%;
}

.confirmedchart{
    background-color: rgba(252, 3, 3,0.3);
    border-radius: 5px;
}
.recoveredchart{
    background-color: rgba(0, 128, 0,0.3);
    border-radius: 5px;
}
.activechart{
    background-color: rgba(65, 105, 225,0.3);
    border-radius: 5px;
}
.deceasedchart{
    background-color: rgba(
        77, 92, 92
        ,0.3);
    border-radius: 5px;
}

.totcases{
    padding-right: 30px;
    padding-left: 30px;
    margin-bottom: 50px;
}

.confirmed{

    color: red;
}
.active{
    color: royalblue;
}
.recovered{
    color: green
}
.deceased {
    color: #4d585c;
}
@media only screen and (max-width: 600px) {
    h2{
        font-size: 18px ;
    }

    .totcases{
        margin-bottom: 50px;
        padding-right: 10px;
        padding-left: 10px;
    }
    
    .minichart{
        height: 80px;
        width: 100%;
        left: auto;
        right: auto;
        margin-left: auto;
        margin-right: auto;
    }
}

*{
  margin:0;
}
.App {
  text-align: center;
  font-family: 'Roboto', sans-serif;}


@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

